@keyframes fill-progress {
  0% {
    width: 0;
  }
  100% {
    width: 100%;
  }
}
.muski-drums .bar-button, .muski-bass .bar-button {
  display: flex;
  position: relative;
  overflow: hidden;
  background-color: white;
  color: #000;
}
.muski-drums .bar-button .bar-button-button, .muski-bass .bar-button .bar-button-button {
  margin-right: -15px;
  z-index: 10;
  border-radius: 15px;
}
.muski-drums .bar-button .bar-button-bar, .muski-bass .bar-button .bar-button-bar {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
  background-color: #efefef;
}
.muski-drums .bar-button .bar-button-bar .progress, .muski-bass .bar-button .bar-button-bar .progress {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 0;
  height: 100%;
  background-color: #fffe81;
}
.muski-drums .bar-button.held .bar-button-bar .progress, .muski-bass .bar-button.held .bar-button-bar .progress {
  animation-name: fill-progress;
  animation-play-state: running;
}
.muski-drums .bar-button.in-progress .bar-button-bar .progress, .muski-bass .bar-button.in-progress .bar-button-bar .progress {
  animation-name: fill-progress;
  animation-play-state: running;
}

.muski-drums .btn-control-round, .muski-bass .btn-control-round {
