  border-radius: 50%;
  margin: 5px;
  background-color: #97ff94;
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
  font-size: 0;
}
.muski-drums .btn-control-round:hover, .muski-bass .btn-control-round:hover {
