}
.muski-drums .muski-tempo, .muski-bass .muski-tempo {
  display: inline-block;
}
.muski-drums .muski-tempo .muski-tempo-label, .muski-bass .muski-tempo .muski-tempo-label {
  text-transform: uppercase;
  font-size: 0.85em;
