.muski-drums .muski-drums-controls-panel, .muski-bass .muski-drums-controls-panel {
  display: flex;
  align-items: center;
  padding-left: 8em;
  width: 840px;
}
.muski-drums .muski-drums-controls-panel .btn-control-round-clear, .muski-bass .muski-drums-controls-panel .btn-control-round-clear {
  margin-left: auto;
  margin-right: -3px;
}

.muski-drums .muski-sequencer-matrix th, .muski-bass .muski-sequencer-matrix th {
  font-size: 0.85rem;
  font-weight: normal;
}
.muski-drums .muski-sequencer-matrix td, .muski-bass .muski-sequencer-matrix td {
  width: 40px;
  height: 40px;
  border: 2px solid white;
  background-color: transparent;
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-cell, .muski-bass .muski-sequencer-matrix .muski-sequencer-cell {
  display: block;
  width: 40px;
  height: 40px;
  background-color: #dcdcdc;
  border: none;
}
@keyframes pulsing {
  0% {
    background-color: #dcdcdc;
  }
  33% {
    background-color: #8ffff9;
  }
  100% {
    background-color: #dcdcdc;
  }
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-cell.pulsing, .muski-bass .muski-sequencer-matrix .muski-sequencer-cell.pulsing {
  animation: pulsing ease-in-out 0.2s 0s 1;
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-cell.active, .muski-bass .muski-sequencer-matrix .muski-sequencer-cell.active {
  animation: none;
  outline: 3px solid #fffe81;
  background-color: #fffe81 !important;
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-cell.on, .muski-bass .muski-sequencer-matrix .muski-sequencer-cell.on {
  animation: none;
  background: none;
  background-color: #73e1db !important;
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-cell.active.on, .muski-bass .muski-sequencer-matrix .muski-sequencer-cell.active.on {
  animation: none;
  background: none;
  outline: 3px solid #fffe81;
  background-color: #8ffff9 !important;
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-row-label, .muski-bass .muski-sequencer-matrix .muski-sequencer-row-label {
  padding-right: 1em;
  text-align: right;
  width: 10em;
  overflow: hidden;
}
.muski-drums .muski-sequencer-matrix .muski-sequencer-col-label, .muski-bass .muski-sequencer-matrix .muski-sequencer-col-label {
  text-align: center;
}
.muski-drums.with-ai .muski-sequencer-matrix .muski-sequencer-cell.ai-input, .muski-bass.with-ai .muski-sequencer-matrix .muski-sequencer-cell.ai-input {
  background-color: #efefef;
}
@keyframes pulsing-ai {
  0% {
    background-color: #efefef;
  }
  33% {
    background-color: #8ffff9;
  }
  100% {
    background-color: #efefef;
  }
}
.muski-drums.with-ai .muski-sequencer-matrix .muski-sequencer-cell.ai-input.pulsing, .muski-bass.with-ai .muski-sequencer-matrix .muski-sequencer-cell.ai-input.pulsing {
  animation: pulsing-ai ease-in-out 0.5s 0s 1;
}

